.carte{
    height: 280px; 
    
}
.cartes{
    border: 4px groove black;
    
}
.card-body{
    color: aliceblue;
}

.dropdowns{
    width: 250px;
    position: relative;
}

.dropdowns .dropdowns-btn{
    padding: 15px 20px;
    background: white;
    box-shadow: 3px 3px 10px 6px rgba(0, 0, 0, 0.06);
    font-weight: bold;
    color:black;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.dropdowns .dropdowns-content{
    position: absolute;
    top: 110%;
    padding: 10px;
    margin: 10px;
    background: white;
    box-shadow: 3px 3px 10px 6px rgba(0, 0, 0, 0.06);
    font-weight: 500;
    color:black;
    z-index: 1000;
}

.dropdowns-content .dropdowns-items{
    padding: 10px;
    cursor: pointer;
}
.dropdowns-items:hover{
    background: rgba(192, 192, 192,0.5);
    padding: 10px 10px;
}