.blockDrop{
    margin: 80px 0 30px 0;
    padding: 40px 0 10px 0;
    height: 450px;
    background: url(../../../public/media/img/accueil/bg.png);
}


.searchY  input{
    width: 600px;
    padding: 10px;
}

.EcoleBlockImg{
    height: 280px;
    background: white;
}

.nomEcole{
    width: 225px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}


.bg-ecoles img{
    width: 100%;
    overflow: hidden;
    position: relative;
    right: 0;
    height: 450px;
    top: -50px;
}

.imgaAvatar{
    position: relative;
}

.avatarEcole{
    position: absolute;
    bottom: 50px;
    background:black;
    height: 180px;
    width: 160px;
    padding: 5px;
    border-radius: 50%;
    left: 50px;
}

.infoEcole{
    position: relative;
    bottom: 50px;
    right: 20px;
}

.lieuEcl{
    position: absolute;
    top: 35px;
    left: 320px;
}

.FbtnSpace button.active{
    border-bottom: 2px solid blue;
}
