.carOffre{
    border-radius: 15px;
    width: 98%;
}


.imgOffre{
    width: 50px;
    height: 50px;
    padding: 5px;
    border-radius: 10px;
}

.card{
    color:white;
    padding: 5px 0;
    margin: 5px 0;
}

.emploie{
    width: 145px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color:blue;
    font-weight: bold;
}

.dropdowns{
    width: 80px;
    position: relative;
}

.dropdowns .dropdowns-btn{
    padding: 10px 10px;
    background: white;
    box-shadow: 3px 3px 10px 6px rgba(0, 0, 0, 0.06);
    font-weight: bold;
    color:black;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.dropdowns .dropdowns-content{
    position: absolute;
    top: 110%;
    padding: 10px;
    margin: 10px;
    background: white;
    box-shadow: 3px 3px 10px 6px rgba(0, 0, 0, 0.06);
    font-weight: 500;
    color:black;
    z-index: 1000;
}

.dropdowns-content .dropdowns-items{
    padding: 10px;
    cursor: pointer;
}
.dropdowns-items:hover{
    background: rgba(192, 192, 192,0.5);
    padding: 10px 10px;
}



.selectioned{
    height: 480px;
    position: sticky;
    right: 10px;
    top: 90px;
    overflow: scroll;
}


.blockOffres{
    margin: 80px 0 30px 0;
    padding: 40px 0 10px 0;
    height: 300px;
    background: url(../../../public/media/img/accueil/bg.png);
}